/**
 * Ïù¥Î©îÏùº Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨
 */
export function validateEmail(email: string): boolean {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return emailRegex.test(email);
}

/**
 * ÎπÑÎ∞ÄÎ≤àÌò∏ Í∞ïÎèÑ Í≤ÄÏÇ¨
 */
export function validatePassword(password: string): {
  isValid: boolean;
  errors: string[];
} {
  const errors: string[] = [];
  
  if (password.length < 8) {
    errors.push('ÎπÑÎ∞ÄÎ≤àÌò∏Îäî 8Ïûê Ïù¥ÏÉÅÏù¥Ïñ¥Ïïº Ìï©ÎãàÎã§.');
  }
  
  if (!/[A-Z]/.test(password)) {
    errors.push('ÎåÄÎ¨∏ÏûêÎ•º Ìè¨Ìï®Ìï¥Ïïº Ìï©ÎãàÎã§.');
  }
  
  if (!/[a-z]/.test(password)) {
    errors.push('ÏÜåÎ¨∏ÏûêÎ•º Ìè¨Ìï®Ìï¥Ïïº Ìï©ÎãàÎã§.');
  }
  
  if (!/[0-9]/.test(password)) {
    errors.push('Ïà´ÏûêÎ•º Ìè¨Ìï®Ìï¥Ïïº Ìï©ÎãàÎã§.');
  }
  
  if (!/[!@#$%^&*]/.test(password)) {
    errors.push('ÌäπÏàòÎ¨∏ÏûêÎ•º Ìè¨Ìï®Ìï¥Ïïº Ìï©ÎãàÎã§.');
  }
  
  return {
    isValid: errors.length === 0,
    errors
  };
}

