/**
 * ÌÜ†ÌÅ∞ ÎßåÎ£å ÏãúÍ∞Ñ Í≥ÑÏÇ∞
 */
export function isTokenExpired(expiresAt: string | number): boolean {
  const now = Date.now();
  const expiry = typeof expiresAt === 'string' ? new Date(expiresAt).getTime() : expiresAt;
  return now >= expiry;
} 